<script>
import { onMounted, reactive, ref } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";
export default {
  setup() {
    let caver = null;
    let contract = null;

    let data = reactive({
      subaddr: "",
      price: 0,
      projectName: "",
      artistAddress: "",
      count: 0,
      projectId: 0,
      bool: false,
      interval: 0,
      limit: 0,
      blockNumber: 0,
      name: "",
      url: "",
      description: "",
      license: "",
      invocation: 0,
      percentage: 0,
      website: "",
    });

    let show = reactive({
      addProject: false,
      airDropMint: false,
      addWhitelist: false,
      removeWhitelist: false,
      toggleWhitelistMintEnabled: false,
      toggleProjectIsActive: false,
      toggleProjectIsLocked: false,
      updateAntibotInterval: false,
      updateMintLimitPerBlock: false,
      updateMintStartBlockNumber: false,
      updateProjectArtistAddress: false,
      updateProjectArtistName: false,
      updateProjectBaseIpfsURI: false,
      updateProjectDescription: false,
      updateProjectLicense: false,
      updateProjectMaxInvocations: false,
      updateProjectName: false,
      updateProjectPricePerTokenInPeb: false,
      updateProjectWhitelistPrice: false,
      updateProjectWebsite: false,
    });

    onMounted(async () => {
      caver = new Caver(window.klaytn);

      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      await contract.methods
        .isOwner()
        .call({
          from: window.klaytn.selectedAddress,
        })
        .then((result) => {
          if (result) {
            getProjectData(0);
          } else {
            window.location.replace(process.env.VUE_APP_CILENT_URL);
          }
        })
        .catch((err) => {
          console.log(err);
        });
      _getCurrentBlock();
    });
    const updateProjectSecondaryMarketRoyaltyPercentage = async (
      _projectId,
      _royalty
    ) => {
      await contract.methods
        .updateProjectSecondaryMarketRoyaltyPercentage(
          Number(_projectId),
          Number(_royalty)
        )
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    };

    const addProject = async (
      _projectName,
      _artistAddress,
      _pricePerTokenInPeb
    ) => {
      const peb = caver.utils.toPeb(_pricePerTokenInPeb, "KLAY");
      await contract.methods
        .addProject(String(_projectName), _artistAddress, caver.utils.toBN(peb))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const airDropMint = async (_address, _requestedCount, _projectId) => {
      await contract.methods
        .airDropMint(_address, Number(_requestedCount), Number(_projectId))
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const addWhitelist = async (_addr) => {
      await contract.methods
        .addWhitelist(_addr)
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const removeWhitelist = async (_addr) => {
      await contract.methods
        .removeWhitelist(_addr)
        .send({
          from: window.klaytn.selectedAddress,
          gas: "20000000",
